.banner {
  margin-top: -66px;
  background: url("../img/bg-back-mobile@1x.jpg") no-repeat  50% 0;
  padding: 100px 0 87px;
  background-size: cover;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-back-mobile@2x.jpg");
  }
}

.banner__wrapper {
  @include wrapper-mb;
  margin: 0 auto;
}

.banner__text {
  font: 300 24px/24px $open-sans;
  color: #ffffff;
  text-align: center;
  margin: 0;
}

@media (min-width: $tablet-width) {
  .banner {
    margin-top: -110px;
    background-image: url("../img/bg-back-tablet@1x.jpg");
    padding: 190px 0 150px;
    background-size: 1200px auto;


    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-back-tablet@2x.jpg");
    }
  }

  .banner__text {
    font: 300 50px/50px $open-sans;
    margin: 0;
  }

  .banner__wrapper {
    @include wrapper-tb;
  }
}

@media (min-width: $desktop-width) {
  .banner {
    background: url("../img/bg-back-desktop@1x.jpg") 50% 0;
    background-size: 2560px auto;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-back-desktop@2x.jpg");
    }
  }

  .banner__wrapper {
    @include wrapper-dt;
  }
}
